.gameItem {
  text-decoration: none;
  color: white;
  width: 320px;
  height: 300px;
  background-color: rgb(36, 37, 41);
  border-radius: 15px;
  position: relative;
  margin: 10px 15px 10px;
}

.gameItem__title {
  font-size: 18px;
  font-weight: bold;
}

.gameItem__genre {
  margin-top: 10px;
  display: flex;
  flex-wrap: wrap;
}

.gameItem__details {
  padding: 10px 20px;
}

.gameItem__description {
  margin-top: 10px;
  font-size: 14px;
}

.gameItem__buy {
  position: absolute;
  right: 0;
  bottom: -12px;
}

.gameBuy__price {
  margin-right: 5px;
  font-weight: bold;
}
