.header {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  padding: 20px 20px;
  border-radius: 6px;
  border: 2px solid darkorchid;
  margin-top: 10px;
  position: relative;
}

.countGame {
  background-color: brown;
  color: white;
  position: absolute;
  top: 45px;
  right: 1px;
  border-radius: 23px;
  width: 25px;
  height: 25px;
  text-align: center;
}
